.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

* { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box; }

.visuallyHidden:not(:focus):not(:active) { position: absolute; width: 1px; height: 1px; margin: -1px;border: 0;padding: 0;white-space: nowrap;clip-path: inset(100%);clip: rect(00 0 0);overflow: hidden;}

/* ---------------------------------------- */
/* variables */
:root {
  --black: #222;
  --darkgray: #333;
}

/* default settings */
body {
  margin: 0;
  background-color: var(--black);
}

img {
  max-width: 100%;
}

/* section */
section {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

/* controller & buttons */
.controller {
  margin: 0 auto;
  padding: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

button {
  border: none;
  background-color: transparent;
  background-repeat: no-repeat;
  background-size: cover;
  width: 51px;
  height: 50px;
  margin: 0;
  padding: 0;
}

/* zoomin button */
.zoomin_button {
  background-image: url('./ui_images/zoomin_rest.png');
}

.zoomin_button:hover,
.zoomin_button:focus {
  background-image: url('./ui_images/zoomin_hover.png');
}

.zoomin_button:active {
  background-image: url('./ui_images/zoomin_pressed.png');
}

/* zoomout button */
.zoomout_button {
  background-image: url('./ui_images/zoomout_rest.png');
}

.zoomout_button:hover,
.zoomout_button:focus {
  background-image: url('./ui_images/zoomout_hover.png');
}

.zoomout_button:active {
  background-image: url('./ui_images/zoomout_pressed.png');
}

/* reset button(home) */
.reset_button {
  background-image: url('./ui_images/home_rest.png');
}

.reset_button:hover,
.reset_button:focus {
  background-image: url('./ui_images/home_hover.png');
}

.reset_button:active {
  background-image: url('./ui_images/home_pressed.png');
}

/* fullPage button */
.fullpage_button {
  background-image: url('./ui_images/fullpage_rest.png');
}

.fullpage_button:hover,
.fullpage_button:focus {
  background-image: url('./ui_images/fullpage_hover.png');
}

.fullpage_button:active {
  background-image: url('./ui_images/fullpage_pressed.png');
}

/* OSD viewer(image container) */
.container {
  width: 100%;
  min-height: 60vh;
}

canvas {
  width: 100%;
}

/* decorative bar at the bottom */
.deco_bar {
  min-height: 15vh;
  width: 100%;
  background-color: var(--darkgray);
}

/* media queries */
@media (min-width: 1100px) {
  nav {
    align-self: flex-start;
  }

  .container {
    min-height: 70vh;
  }
}

